<template>
  <div>
    <section>
      <v-layout column align-center>
        <img class="myimg" src="../assets/logo.png">
        <h1 class="white--text mb-2 display-4 font-weight-light text-xs-center">Vitasenior-mt</h1>
        <br>
        <v-btn @click="installer" :disabled="installed">{{installed?'instalado':'instalar'}}</v-btn>
      </v-layout>
    </section>
  </div>
</template>

<script>
export default {
  data: () => {
    return {
      imgsrc: "../assets/logo.png",
      installed: true,
      installer: undefined
    };
  },
  created() {
    let installPrompt;

    window.addEventListener("beforeinstallprompt", e => {
      e.preventDefault();
      installPrompt = e;
      this.installed = false;
    });

    this.installer = () => {
      this.installed = true;
      installPrompt.prompt();
      installPrompt.userChoice.then(result => {
        if (result.outcome === "accepted") console.log("User accepted");
        else console.log("User denied");
        installPrompt = null;
      });
    };
  }
};
</script>

<style>
.myimg {
  height: 100px !important;
  width: 100px !important;
}
</style>




